@charset "UTF-8";
html {
  height: 100%;
}

body,
html {
  margin: 0;
  font-family: "Arial", "Helvetica", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  border-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  /* убирает пробелы, не дает переносить текст на другие строки */
  clip-path: inset(100%);
  /* определяет область отображения текста по шаблону. совместимость средняя */
  clip: rect(0 0 0 0);
  /* определяет область отображения текста по прямоугольнику. совместимость высокая */
  overflow: hidden;
  /* определяет что делать с элементами, которые не вмещаются в блок: показать/скрыть/скролл. можно применять к блокам, флексам и гридам */
}

a, ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

.main-page {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 19px;
  line-height: 23px;
  background: #ece8dd url("../../../src/images/fone.gif") left top repeat;
}

.main-page__container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  background-color: gray;
  background: gray url("../../../src/images/content-fone.jpg") -330px 330px repeat;
}

.hero__container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.hero__container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}

.main-page__hero-image {
  position: relative;
  z-index: 2;
}

.hero {
  max-width: 1440px;
  min-height: 675px;
}

.hero__image {
  width: 100%;
  height: auto;
}

.hero__header {
  position: relative;
  margin: 0 auto;
  margin-bottom: 58px;
  z-index: 2;
}

.main-page-header {
  max-width: 1300px;
  display: flex;
  padding: 25px 75px;
  align-items: center;
  justify-content: space-between;
}

.main-page-header__logo {
  margin-left: 70px;
}

.footer__logo {
  margin-right: 20px;
}

.logo {
  display: block;
  width: 63px;
  height: 63px;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
  will-change: transform;
  transition: 0.5s transform, 0.5s -webkit-transform;
}
.logo:hover {
  z-index: 3;
  transform: scale(1.2);
}

.logo__image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.user-block__item {
  margin-right: 20px;
}
.user-block__item:last-child {
  margin-right: 0;
}

.user-avatar {
  width: 63px;
  height: 63px;
  border-radius: 50%;
  overflow: hidden;
  will-change: transform;
  transition: 0.5s transform, 0.5s -webkit-transform;
}
.user-avatar:hover {
  transform: scale(1.2);
}

.user-avatar__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.user-block {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.user-block-text {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  color: #e3e319;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  transition: 0.5s color;
}
.user-block-text:hover {
  color: #ffff1b;
  animation: pulse 1s infinite;
}
@keyframes pulse {
  0% {
    text-shadow: 0 0 25px rgba(255, 255, 255, 0.9);
  }
  50% {
    text-shadow: 0 0 50px rgba(255, 255, 255, 0.9);
  }
  100% {
    text-shadow: 0 0 25px rgba(255, 255, 255, 0.9);
  }
}

.main-page__film-card {
  position: relative;
  margin: 0 auto;
  z-index: 2;
}

.film-card {
  max-width: 1300px;
  padding: 25px 75px;
  display: flex;
}

.film-card__poster {
  margin-right: 40px;
}

.film-card__info {
  position: relative;
  display: flex;
  flex-direction: column;
}

.film-card__image {
  width: 218px;
  max-width: 100%;
  height: auto;
}

.film-card__title {
  font-size: 32px;
  line-height: 45px;
  color: #ffff1b;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  max-width: 750px;
  max-height: 85px;
  overflow: hidden;
}

.film-card__meta {
  font-size: 19px;
  line-height: 23px;
  font-weight: 700;
  color: #ffff1b;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  margin-bottom: 25px;
}

.film-card__genre::after {
  content: "·";
  margin-left: 4px;
  margin-right: 4px;
}

.film-card__buttons {
  display: flex;
  max-width: 700px;
  max-height: 300px;
  overflow: hidden;
}

.film-card__button {
  margin-right: 14px;
}
.film-card__button:last-child {
  margin-right: 0px;
}

.button {
  display: flex;
  font-size: 19px;
  line-height: 23px;
  color: #ffff1b;
  background: rgba(55, 54, 41, 0.65);
  align-items: center;
  padding: 13px 30px;
  min-width: 128px;
  max-width: 300px;
  overflow: hidden;
  border-radius: 8px;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  will-change: transform;
  transition: 0.2s background-color, 0.2s transform, 0.2s -webkit-transform;
}
.button:hover {
  background: rgba(0, 0, 0, 0.51);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.button svg {
  vertical-align: top;
  margin-right: 9px;
}

.main-page__content {
  width: 1200px;
}

.main-page-content__catalog {
  width: 100%;
  padding: 20px 75px;
}

.catalog {
  width: 1200px;
  max-width: 1200px;
}

.catalog__genres-list {
  margin-bottom: 16px;
}

.genres-list {
  width: 100%;
}

.genres-list__item {
  position: relative;
  display: inline-block;
  min-width: 80px;
  max-width: 250px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 0 10px;
}

.genres-list__link {
  display: block;
  font-size: 19px;
  line-height: 23px;
  color: #000;
  will-change: transform;
  transition: transform 0.2s linear;
  padding-bottom: 8px;
  margin-bottom: 1px;
}
.genres-list__link:hover {
  transform: scale(1.1);
}
.genres-list__link--active::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  bottom: 0;
  background: red;
}

.catalog__films-list {
  width: 1050px;
}

.films-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.films-list__films-card {
  margin-right: 10px;
  margin-bottom: 10px;
}

.small-film-card {
  position: relative;
  width: calc((100% - 40px) / 4);
  border-radius: 6px;
  overflow: hidden;
  cursor: pointer;
  will-change: transform;
  transition: 0.2s transform, 0.2s -webkit-transform;
}
.small-film-card:hover {
  z-index: 3;
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.small-film-card__image {
  display: block;
  position: relative;
  z-index: 1;
}
.small-film-card__image::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0.63) 97%);
}

.small-film-card__img {
  width: 250px;
  height: auto;
  max-height: 160px;
  object-fit: cover;
}

.small-film-card__title {
  z-index: 2;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0 10px;
  font-size: 17px;
  line-height: 20px;
  color: #ffff1b;
}
.small-film-card__title:hover {
  z-index: 3;
  animation: pulse 1s infinite;
}
@keyframes pulse {
  0% {
    text-shadow: 0 4px 6px rgba(255, 255, 27, 0.7);
  }
  50% {
    text-shadow: 0 3px 4px rgba(255, 255, 27, 0.7), 0 6px 10px rgba(255, 165, 0, 0.7);
  }
  100% {
    text-shadow: 0 4px 6px rgba(255, 255, 27, 0.7);
  }
}

.small-film-card__link {
  color: inherit;
}

.more-films__button {
  display: block;
  width: 100%;
  padding: 29px 40px;
  background: none;
  border: 1px solid rgba(44, 43, 39, 0.2);
  border-radius: 8px;
  transition: border-color 0.5s;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  color: #000;
  cursor: pointer;
}
.more-films__button:hover {
  border-color: #000;
}

.main-page__footer {
  margin-top: 100px;
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  max-width: 1400px;
}

.copypright {
  font-size: 19px;
  line-height: 23px;
  color: #000;
}

.movie-page__film-card {
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
}

.movie-page__film-card-info {
  align-items: center;
}

.movie-page__additional-film-info {
  position: relative;
  z-index: 2;
  transform: translateY(-170px);
  color: white;
}

.additional-film-info {
  max-width: 1300px;
  padding: 25px 75px;
  padding-bottom: 60px;
  display: flex;
}

.additional-film-info__poster {
  margin-right: 40px;
  min-width: 218px;
}

.additional-film-info__image {
  width: 218px;
  max-width: 100%;
  height: auto;
}

.additional-film-info__info {
  padding-top: 100px;
}

.additional-film-info__film-menu {
  margin-bottom: 40px;
}

.film-menu {
  display: flex;
  flex-direction: row;
}

.film-menu__item {
  position: relative;
  display: inline-block;
  min-width: 80px;
  max-width: 250px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: 20px;
}
.film-menu__item:nth-last-child(1) {
  margin-right: 0;
}

.film-menu__link {
  display: inline-block;
  min-height: 35px;
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  color: #ffff1b;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.film-menu__item--active a {
  border-bottom: 2px solid #ffff1b;
}

.overview__rating-value {
  font-size: 25px;
  line-height: 30px;
  padding: 8px 12px;
  color: #000;
  background: rgba(0, 0, 0, 0.24);
  border-radius: 8px;
}

.overview__rating {
  margin-bottom: 30px;
}

.overview__rating-text-value {
  color: #000;
  font-size: 19px;
  line-height: 23px;
  font-weight: 700;
}

.overview__rating-count {
  color: #000;
  font-size: 19px;
  line-height: 23px;
}

.overview__description {
  margin-bottom: 30px;
}

.overview__description-text {
  color: #000;
  font-size: 19px;
  line-height: 23px;
}

.overview__director {
  margin-bottom: 30px;
}

.overview__director-title {
  color: #000;
  font-size: 19px;
  line-height: 23px;
  font-weight: 700;
}

.overview__director-name {
  color: #000;
  font-size: 19px;
  line-height: 23px;
}

.overview__starring-title {
  color: #000;
  font-size: 19px;
  line-height: 23px;
  font-weight: 700;
}

.overview__starring-names {
  color: #000;
  font-size: 19px;
  line-height: 23px;
}

.movie-page__more-films {
  width: 1200px;
  transform: translateY(-170px);
  padding: 20px 75px;
  border-top: 2px solid #000;
}

.more-films {
  width: 1200px;
}

.more-films__title {
  font-size: 32px;
  line-height: 45px;
  text-decoration: underline dotted black;
  text-underline-offset: 5px;
}

.movie-page__catalog {
  width: 100%;
  padding: 20px 75px;
}

.movie-page__footer {
  margin-top: -50px;
}

.details {
  max-width: 792px;
}

body {
  background-color: white;
}